import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import {BrowserRouter as Router, Route, Link} from 'react-router-dom'
import Beranda from './pages/Beranda'
import Budaya from './pages/Budaya'
import Alam from './pages/Alam'

class App extends Component {
  render() {
    return (
      <Router>
        <div>
          <nav className="navbar navbar-expand-lg navbar-light bg-light fixed-top">
            <Link className="navbar-brand" to="/">Pesona Nusantara</Link>
            <div className="navbar-nav">
              <Link className="nav-item nav-link" to="/">Beranda</Link>
              <Link className="nav-item nav-link" to="/budaya">Budaya Nusantara</Link>
              <Link className="nav-item nav-link" to="/alam">Alam Nusantara</Link>
            </div>
          </nav>
          <div style={{paddingTop: 60}}>
          <Route path="/" exact component={Beranda}></Route>
          <Route path="/budaya" component={Budaya}></Route>
          <Route path="/alam" component={Alam}></Route>
          </div>
        </div>
      </Router>
    );
  }
}

export default App;
