import React from 'react'
import Intro from '../components/Intro'
import Card from '../components/Card'
import Layout from './Layout'

const intro={
    content: 'Indonesia merupakan negara yang memiliki berjuta keindahan Panorama didalamnya. Kekayaan akan sumber daya alamnya merupakan daya tarik tersendiri bagi nerga-negara lain. Tentunya pemandangan dan keindahan alamnya juga banyak dan beragam, mulai dari sabang sampai merauke terdapat keindahan alam yang tersembunyi yang tidak dimiliki oleh negara lain. Maka dari itu cintailah dan rawatlah alam yang kita miliki ini.'
}

const cards = [
    {
        imageUrl: 'https://cdn02.indozone.id/re/content/2020/03/05/WYsdVo/t_5e60f929704b0.jpg?w=700&q=85',
        title   : 'Gunung-Gunung di Nusantara', 
        subtitle: 'Kamu bisa menjelajahi gunung-gunung yang ada di pulau Nusantara'
    },
    {
        imageUrl: 'http://www.infowakatobi.com/wp-content/uploads/2017/11/wisata-air-wakatobi.jpg',
        title   : 'Keindahan bawah laut di Nusantara', 
        subtitle: 'Kamu bisa menyelam untuk melihat keindahan alam surga bawah laut'
    },
]

const Alam =() =>(
    <div className="container">
        <h1 style={{textAlign: 'center'}}>ALAM NUSANTARA</h1>
        <div className="row" style={{marginTop: 40, marginBottom: 100}}>
            <Intro content={intro.contentAlam}/>
            <div className="col-md-6">
            <img
                src="https://blue.kumparan.com/image/upload/fl_progressive,fl_lossy,c_fill,q_auto:best,w_640/v1513052742/gmchoaqksvmytotaqskz.jpg"
                alt="" />
            </div>
        </div>
        <div className="row" style={{marginBottom: 50}}>
            {
                cards.map(card =>
                    <Card
                    imageUrl={card.imageUrl}
                    title={card.title}
                    subtitle={card.subtitle}
                    />
                    )
            }
        </div>
    </div>
)

export default Alam