import React from 'react'

const Beranda =() => (
    <div style={styles.container}>
        <img style={styles.imageCover}
        src="https://adroittravels.com/wp-content/uploads/2019/10/banner.jpg"
        alt="sr12" />
        <div style={styles.blockText}>
            <h1>Jelajahi Nusantara Secara Virtual</h1>
            <h4>Seluruh dunia tertuju pada Pesona Nusantara.</h4>
        </div>
    </div>
)
const styles={
    container:{
        position:'relative',
        textAlign:'center',
    },
    imageCover:{
        width: '100%',
        opacity: '0.6',
    
    },
    blockText: {
        width: '100%',
        position: 'absolute',
        top: '50%',
        left: '50%',
        transform: 'translate(-50%, -50%)',
        color: 'black'
    }
}

export default Beranda