import React from 'react'
import Card from '../components/Card'
import Intro from '../components/Intro'

const intro ={
    content: 'Kebudayaan nusantara yaitu kebudayaan yang diakui sebagai identitas nasional Nusantara dan sebagai perwujudan cipta, karya dan karsa bangsaÂ dan merupakan keseluruhan daya upaya manusia Indonesia untuk mengembangkan harkat dan martabat sebagai bangsa.'
}

const features =[
    {title: 'Mengenal Rumah Tradisional Nusantara', desc:'Kamu bisa mengenal berbagai rumah tradisional nusantara dari sabang sampai merauke'},
    {title: 'Mempelajari Tarian Tradisional Nusantara', desc:'Kamu bisa mempelajari berbagai tarian tradisional di masing-masing daerah nusantara'},
    {title: 'Mengenal Pakaian Adat Nusantara', desc:'Kamu bisa mengenal berbagai macam kostum tradisional masing-masing daerah nusantara'},
]

const cards=[
    {title: 'Rumah Tradisional Nusantara', imageUrl:'https://c7.alamy.com/comp/C93KGK/rumah-gadang-minangkabau-culture-big-house-on-sumatra-C93KGK.jpg'},
    {title: 'Tarian Tradisional Nusantara', imageUrl:'https://i.pinimg.com/236x/01/a1/36/01a13665b7fb23991584f0c6bc8fe35c--tarian-travers.jpg'},
    {title: 'Pakaian Adat Nusantara', imageUrl:'https://oss.beautynesia.id/photo/5d9d7b25a8888670d8e7eb94?x-oss-process=style/_m'},
]

const Budaya = () => (
    <div className="container">
        <h1 style={{textAlign:'center'}}>BUDAYA NUSANTARA</h1>
        <div className="row" style={{marginTop: 40, marginBottom: 100}}>
            <Intro content={intro.content}/>
            <div className="col-md-6" style={styles.box450border}>
                {
                    features.map(feature => (
                        <div>
                            <h5><strong>{feature.title}</strong></h5>
                            <p>{feature.desc}</p>
                        </div>
                    ))
                }
            </div>
        </div>
        <div className="row" style={{marginBottom: 50}}>
            {
                cards.map(card =>
                    <Card
                    imageUrl={card.imageUrl}
                    title={card.title}
                    />
                    )
            }
        </div>
    </div>
)

const styles={
    box450border:{
        height: '450px',
        border: '2px solid #f1f1f1',
        borderRadius: 10,
        padding: 30
    },
}

export default Budaya